import { GeocodingService } from './../../utils/geocoding.service';
import { DataManagementService } from './../../providers/data-management.service';
import { MapService } from './../../utils/map.service';
import { PointInterest } from './../../objects/data-management';
import { Component,Input, Output } from '@angular/core';
import { NavController, NavParams, ModalController,LoadingController,ViewController,ToastController } from 'ionic-angular';
import Polygon = L.Polygon;
import Marker = L.Marker;
import Icon = L.Icon;
import {imagesDir} from "../../providers/global.config";

 @Component({
    selector: 'app-poi',
    templateUrl: 'poi.html'
})
export class Poi{
    mode: string = 'ADD';
    
        @Input()
        pointInterest: PointInterest = new PointInterest();
    
    pointInterests: PointInterest[] = [];
    
    pointInterestsPoint: PointInterest[] = [];
    pointInterestsPolygon: PointInterest[] = [];
    images = [
        {text: "marker1", uri: imagesDir + "marker1.png"},
        {text: "marker2", uri: imagesDir + "marker2.png"},
        {text: "marker3", uri: imagesDir + "marker3.png"},
        {text: "marker4", uri: imagesDir + "marker4.png"},
        {text: "marker5", uri: imagesDir + "marker5.png"},
        {text: "marker6", uri: imagesDir + "marker6.png"},
        {text: "marker7", uri: imagesDir + "marker7.png"},
        {text: "marker8", uri: imagesDir + "marker8.png"},
        {text: "marker9", uri: imagesDir + "marker9.png"},
        {text: "marker10", uri: imagesDir + "marker10.png"},
        {text: "marker11", uri: imagesDir + "marker11.png"},
        {text: "flag1", uri: imagesDir + "flag1.png"},
        {text: "flag2", uri: imagesDir + "flag2.png"},
        {text: "house1", uri: imagesDir + "house1.png"},
        {text: "house2", uri: imagesDir + "house.png"}
    ];
    nompoi:any;
    rayonpoi:any;
    selectedImage: { text: string, uri: string } = this.images[0];
    addresspoi:string;
    latpoi:number;
    lngpoi:number;
    imageuripoi:any;
    constructor(public navParams: NavParams,private mapService: MapService,public toastr: ToastController,private dataManagementService: DataManagementService, public viewCtrl: ViewController,private geocodingService: GeocodingService) {
        this.addresspoi=this.navParams.get('addresspoi');
        this.latpoi=this.navParams.get('latpoi');
        this.lngpoi=this.navParams.get('lngpoi');
        console.log(this.latpoi);
    }
   
    ngOnInit() {

        this.loadPOI();
        
    }

    //GET list of Points and Polygons
    loadPOI() {
        if (this.dataManagementService.pointInterests) {
            this.dataManagementService.pointInterests.forEach(data=> {
                  
                if (data.type == "MARKER") {
                    this.pointInterestsPoint.push(data);
                }
                else if (data.type == "POLYGON") {
                    this.pointInterestsPolygon.push(data);
                }
            });
           
        }
        else {
            this.dataManagementService.getAllPointInterests().subscribe(response => {
                response.forEach(data=> {
                    
                    if (data.type == "MARKER") {
                        this.pointInterestsPoint.push(data);
                       
                    }
                    else if (data.type == "POLYGON") {
                        this.pointInterestsPolygon.push(data);
                    }
                    
                });
                this.dataManagementService.pointInterests = response;
              
            });
        }
        

    }
    displayPointPolygon(pointInterest: PointInterest) {
        
                this.mapService.removeMarkersFromMap();
                this.mapService.removeMarkersPoiFromMap();
                this.mapService.removePolygonsPoiFromMap();
        
                let popup = '<b>Nom : '+pointInterest.name+'</b> <br> <b>Adresse:'+pointInterest.address+'</b>';
                let marker = null;
        
                if (pointInterest.type == "POLYGON") {
                    let polygon = new Polygon(pointInterest.decode);
                    //this.mapService.addPolygon(polygon);
                    this.mapService.addPolygonPoi(polygon);
        
                }
        
                marker = new Marker(pointInterest.coordinate);
                marker.on("click", () => {
                    this.mapService.map.setView(pointInterest.coordinate, 17);
                });
        
                //Edit Marker 
                marker.options.draggable = true;
                this.pointInterest = pointInterest;
                marker.on("dragend", (e: any) => {
        
                    this.pointInterest.coordinate = e.target._latlng;
                    this.geocodingService.inverseGeoconding(this.pointInterest.coordinate.lat, this.pointInterest.coordinate.lng, 17).subscribe(adress => {
                        this.pointInterest.address = adress.display_name;
                    });
                   
                });
        
                marker.bindPopup(popup);
                marker.setIcon(new Icon({
                    iconUrl: pointInterest.imageUri,
                    iconAnchor: [-2, 10],
                    popupAnchor: [10, -25]
                }));
                this.mapService.addMarkerPoi(marker);
                this.mapService.map.setView(pointInterest.coordinate, 12);
                this.closeModal();
               
            }
    closeModal(){
        this.viewCtrl.dismiss(null);
        
      }
  drawPointInterest(pointInterest: PointInterest) {
        let popup = "<span class='leaflet-pelias-layer-icon-container'><div class='leaflet-pelias-layer-icon leaflet-pelias-layer-icon-point' title='layer: venue'></div></span> Nom : <strong>" + pointInterest.name + "</strong><br><hr><b>Adresse : " + pointInterest.address + "</b>";
        let marker = null;

        if (pointInterest.type == "POLYGON") {
            let polygon = new Polygon(pointInterest.decode);
            //this.mapService.addPolygon(polygon);
            this.mapService.addPolygonPoi(polygon);
        }

        marker = new Marker(pointInterest.coordinate);
        marker.on("click", () => {
            this.mapService.map.setView(pointInterest.coordinate, 17);
        });

        marker.on("mouseover", () => {
            marker.openPopup();
        });

       
        marker.setIcon(new Icon({
            iconUrl: pointInterest.imageUri,
            iconAnchor: [-2, 10],
            popupAnchor: [10, -25]
        }));
        marker.bindPopup(popup);
        this.mapService.addMarkerPoi(marker);
    }
    addpoi(){
        let pointInterest= new PointInterest();
        pointInterest.address=this.addresspoi;
        pointInterest.name=this.nompoi;
        pointInterest.ray=this.rayonpoi;
        pointInterest.coordinate={lat:this.latpoi,lng:this.lngpoi};
        
        console.log(pointInterest.coordinate.lat)
        pointInterest.type='MARKER';
        pointInterest.imageUri=this.selectedImage.uri;
        console.log(pointInterest)
        this.viewCtrl.dismiss({pointInterest:pointInterest });
      }
      addPointInterest() {
        this.viewCtrl.dismiss(null);
                this.pointInterest.imageUri = this.selectedImage.uri;
        
                this.dataManagementService.addPointInterest(this.pointInterest).subscribe(pointInterest => {
        
                    this.drawPointInterest(pointInterest);
        
                    if (pointInterest.type == "MARKER") {
                        let circle = L.circle(this.pointInterest.coordinate, {
                            color: 'red',
                            fillColor: '#f03',
                            fillOpacity: 0.1,
                            radius: this.pointInterest.ray
                        });
                        this.mapService.addCircle(circle);
        
                        setTimeout(()=> {
                            this.mapService.removeCirclesFromMap();
                        }, 3000);
                    }
        
                    this.dataManagementService.pointInterests.push(pointInterest);
                   
                   
                }, (err) => {
                   
                });
        
            }
            
            drawPointInterests() {
                
                        this.mapService.removeMarkersPoiFromMap();
                        this.mapService.removePolygonsPoiFromMap();
                        this.mapService.removeCirclesFromMap();
                
                       
                       
                
                            if (this.dataManagementService.pointInterests == null) {
                                this.dataManagementService.getAllPointInterests().subscribe(pointInterests => {
                                    this.dataManagementService.pointInterests = pointInterests;
                                    this.pointInterests = pointInterests;
                                    pointInterests.forEach(pointInterest => {
                                        this.drawPointInterest(pointInterest);
                                    });
                                });
                            }
                
                            else {
                                this.dataManagementService.pointInterests.forEach(pointInterest => {
                                    this.drawPointInterest(pointInterest);
                                });
                            }
                
                        
                
                        }
                      
                    

}


