import { Historical } from './../historical/historical';
import { Component } from '@angular/core';
import { NavController, ToastController, LoadingController, App } from 'ionic-angular';
import {Login} from '../../providers/login';
import {RealTimeService} from '../../providers/real-time.service';
import {HistoricalService} from '../../providers/historical.service';
import { DataManagementService } from '../../providers/data-management.service';
import { RealTimeRecord, Group, Vehicule } from "../../objects/real-time";
import { Subscription } from "rxjs";
import { Storage } from '@ionic/storage';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  buttonColor: string = '#c42e36';
  loader:any;
  showing: boolean;
  groups: Group[];
  login: String = null;
  mdp: String = null;
  allGroups: Subscription;
  newRealTimeRecords: RealTimeRecord[] = null;
  oldRealTimeRecords: RealTimeRecord[] = null;
  allRealTimeRecords: Subscription;
  searchWord: string = '';
  loginTest : String = null;
  mdpTest : String = null;
  constructor(public storage: Storage,private historicalService: HistoricalService,public app : App,public loadingCtrl: LoadingController,private realTimeService: RealTimeService,private dataManagementService: DataManagementService, public navCtrl: NavController,private signinService: Login,private toastController: ToastController) {
    this.storage.get('username').then((data)=>{
      if(data != null){ 
        this.login = data;
      }
      this.storage.get('password').then((data)=>{
     if(data != null){              
        this.mdp = data;
     }
     if(this.login != null && this.mdp != null){
      this.loginTest = this.login;
      this.mdpTest = this.mdp;
      this.onSubmit();
    }
    });
    });
    
  }

  onSubmit() {
 
    this.buttonColor = '#345465'; 
    this.loader = this.loadingCtrl.create({
      content: "Veuillez attendre..."
    });
    this.loader.present();
    this.signinService.login({"username":this.login,"password":this.mdp}).subscribe(
      token => {
          this.goToRt();
          this.signinService.token = token.token;
          this.realTimeService.token = token.token;
          this.historicalService.token = token.token;
          this.dataManagementService.token = token.token;
          this.storage.set('username',this.login);
          this.storage.set('password',this.mdp);
        }
      , ()=> {
        let toast = this.toastController.create({
          message: 'Authentification Echouée!',
          duration: 2000
        });
        this.buttonColor = '#c42e36'; 
        toast.present();
        this.loader.dismiss();
      });

  }
  ionViewWillLeave(){
    this.loader.dismiss();
  }

  goToRt(){
    
    this.navCtrl.push(Historical).then(() => {
      const index = this.navCtrl.getActive().index;
      this.navCtrl.remove(0, index);
    });
  
  }
  
}



